<template>
  <div class="detail-wrapper">
    <header class="detail-header">
      <button @click="$emit('go-back')" class="back-btn">‚Üê Î™©Î°ùÏúºÎ°ú ÎèåÏïÑÍ∞ÄÍ∏∞</button>
    </header>

    <div class="content-container" v-if="data">
      <div class="left-panel">
        <h2 class="title">ÏÉÅÌíà ÏÉÅÏÑ∏ Ï†ïÎ≥¥</h2>
        <div class="image-box">
          <img :src="data.imageUrl" @error="e => e.target.src='/placeholder.png'" />
        </div>

        <div class="widget-area">
          <div class="widget-title">Ïú†ÏÇ¨ Ïù¥ÎØ∏ÏßÄ Í≤ÄÏÉâ ÏòµÏÖò</div>
          <SpecFilter
              v-if="candidates.length > 0"
              :products="candidates"
              :targetProduct="data"
              @filter-change="handleFilterChange"
          />
          <button class="search-btn" @click="executeSearch" :disabled="loading">
            {{ loading ? 'Í≤ÄÏÉâ Ï§ë...' : 'üîç Ï°∞Í±¥ÏúºÎ°ú Í≤ÄÏÉâÌïòÍ∏∞ (Top 10)' }}
          </button>
        </div>

        <div class="result-area" v-if="searchResult.length > 0">
          <div style="font-weight: bold; margin-bottom: 5px; font-size: 14px;">Í≤ÄÏÉâ Í≤∞Í≥º ({{ searchResult.length }}Í±¥)</div>
          <div class="result-grid">
            <div class="result-item" v-for="res in searchResult" :key="res.productId"
                 @click="goToProduct(res.productCode)"
                 style="cursor: pointer;">
              <img :src="res.imageUrl"
                   @error="e => e.target.src='/placeholder.png'"
                   class="result-img" />
              <div class="result-name">{{ res.productName }}</div>
              <div class="result-sim">{{ (res.similarity * 100).toFixed(1) }}%</div>
            </div>
          </div>
        </div>
      </div>

      <div class="right-panel">
        <h1 class="product-name">{{ data.name }}</h1>
        <p class="product-code">ÏÉÅÌíàÏΩîÎìú: {{ data.productCode }}</p>

        <div class="spec-section" v-if="data.specifications && Object.keys(data.specifications).length > 0">
          <h3>ÏÉÅÏÑ∏ Ïä§Ìéô</h3>
          <div class="spec-grid">
            <div v-for="(val, key) in data.specifications" :key="key" class="spec-item">
              <span class="spec-key">{{ key }}</span>
              <span class="spec-val">{{ val }}</span>
            </div>
          </div>
        </div>

        <div class="chart-section" v-if="data.priceHistory && data.priceHistory.length > 0">
          <h3>Í∞ÄÍ≤© Î≥ÄÎèô Í∑∏ÎûòÌîÑ</h3>
          <div class="canvas-holder">
            <canvas id="priceChart"></canvas>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { onMounted, defineProps, defineEmits, nextTick, ref, watch } from 'vue';
import axios from 'axios';
import Chart from 'chart.js/auto';
import SpecFilter from './SpecFilter.vue';

const props = defineProps(['data', 'isNewDb']); // isNewDb ÌîÑÎ°≠ Ï∂îÍ∞Ä
const emit = defineEmits(['go-back', 'view-detail']);

// Ïú†ÏÇ¨ Ïù¥ÎØ∏ÏßÄ Í≤ÄÏÉâÏö© ÏÉÅÌÉú
const candidates = ref([]);
const filteredCandidates = ref([]);
const searchResult = ref([]);
const loading = ref(false);
let priceChart = null;

// Í≤ÄÏÉâ Í≤∞Í≥ºÏóêÏÑú ÏÉÅÌíà ÌÅ¥Î¶≠ Ïãú Ï≤òÎ¶¨
const goToProduct = (productCode) => {
  searchResult.value = [];
  window.scrollTo({ top: 0, behavior: 'smooth' });
  emit('view-detail', productCode, props.isNewDb);
};

// ÌïÑÌÑ∞ Î≥ÄÍ≤Ω Ïãú Í≤ÄÏÉâ ÎåÄÏÉÅ ÌïÑÌÑ∞ÎßÅ Î°úÏßÅ
const handleFilterChange = (filters) => {
  filteredCandidates.value = candidates.value.filter(p => {
    return Object.keys(filters).every(key => {
      const selectedOptions = filters[key];
      if (!p.specifications) return false;
      try {
        const specs = typeof p.specifications === 'string'
            ? JSON.parse(p.specifications)
            : p.specifications;
        const productVal = specs[key];
        return productVal && selectedOptions.includes(productVal);
      } catch (e) { return false; }
    });
  });
};

// Python Ïä§ÌÅ¨Î¶ΩÌä∏ Ïó∞Îèô Í≤ÄÏÉâ Ïã§Ìñâ
const executeSearch = async () => {
  if (!props.data) return;
  loading.value = true;
  searchResult.value = [];

  try {
    const filterIdList = filteredCandidates.value.map(p => String(p.productCode));
    if (filterIdList.length === 0) {
      alert("Í≤ÄÏÉâ ÎåÄÏÉÅÏù¥ ÏóÜÏäµÎãàÎã§.");
      loading.value = false;
      return;
    }

    const res = await axios.post('http://localhost:8083/api/similar-images', {
      pcode: String(props.data.productCode),
      top: 10,
      filters: filterIdList
    });

    if (res.data.success) {
      searchResult.value = res.data.similarImages;
    } else {
      alert("Í≤ÄÏÉâ Ïã§Ìå®: " + res.data.error);
    }
  } catch (e) {
    console.error(e);
    alert("Ïú†ÏÇ¨ Ïù¥ÎØ∏ÏßÄ Í≤ÄÏÉâ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
  } finally {
    loading.value = false;
  }
};

// Ï∞®Ìä∏ ÏÉùÏÑ± Ìï®Ïàò
const initChart = async () => {
  if (!props.data || !props.data.priceHistory || props.data.priceHistory.length === 0) return;

  await nextTick();
  const canvas = document.getElementById('priceChart');
  if (!canvas) return;

  if (priceChart) priceChart.destroy(); // Í∏∞Ï°¥ Ï∞®Ìä∏ ÌååÍ¥¥

  const ctx = canvas.getContext('2d');
  const sortedHistory = [...props.data.priceHistory].sort((a, b) => new Date(a.date) - new Date(b.date));

  priceChart = new Chart(ctx, {
    type: 'line',
    data: {
      labels: sortedHistory.map(h => h.date),
      datasets: [{
        label: 'ÏµúÏ†ÄÍ∞Ä Ï∂îÏù¥ (Ïõê)',
        data: sortedHistory.map(h => h.price),
        borderColor: '#42b983',
        backgroundColor: 'rgba(66, 185, 131, 0.1)',
        fill: true,
        tension: 0.3,
        pointRadius: 4,
        pointBackgroundColor: '#42b983'
      }]
    },
    options: {
      responsive: true,
      maintainAspectRatio: false,
      plugins: {
        legend: { display: false },
        tooltip: {
          callbacks: { label: (context) => ` ${context.parsed.y.toLocaleString()}Ïõê` }
        }
      },
      scales: {
        y: { ticks: { callback: (value) => value.toLocaleString() + 'Ïõê' } }
      }
    }
  });
};

onMounted(async () => {
  // 1. Ï∞®Ìä∏ Ï¥àÍ∏∞Ìôî
  initChart();

  // 2. Í≤ÄÏÉâ ÌõÑÎ≥¥Íµ∞ Î°úÎìú (ÌïÑÌÑ∞ Íµ¨ÏÑ±ÏùÑ ÏúÑÌï¥ ÌòÑÏû¨ DB Î™©Î°ù Î°úÎìú)
  const listUrl = props.isNewDb ? 'http://localhost:8083/api/new/product' : 'http://localhost:8083/api/product';
  try {
    const res = await axios.get(listUrl, { params: { size: 1000 } });
    candidates.value = res.data;
    filteredCandidates.value = res.data;
  } catch (e) { console.error("ÌõÑÎ≥¥Íµ∞ Î°úÎìú Ïã§Ìå®", e); }
});

// Îç∞Ïù¥ÌÑ∞Í∞Ä Î∞îÎÄåÎ©¥(Îã§Î•∏ ÏÉÅÌíà ÌÅ¥Î¶≠ Ïãú) Ï∞®Ìä∏ Ïû¨Î†åÎçîÎßÅ
watch(() => props.data, () => {
  initChart();
});
</script>

<style scoped>
.detail-wrapper { padding: 30px; max-width: 1200px; margin: 0 auto; background: #fff; min-height: 100vh; }
.detail-header { display: flex; justify-content: space-between; align-items: center; margin-bottom: 30px; border-bottom: 1px solid #eee; padding-bottom: 20px; }
.back-btn { padding: 10px 20px; border: 1px solid #42b983; background: #fff; color: #42b983; border-radius: 8px; cursor: pointer; font-weight: bold; }
.back-btn:hover { background: #42b983; color: white; }

.content-container { display: flex; gap: 60px; }
.left-panel { flex: 0 0 450px; }
.image-box { margin-top: 20px; }
.image-box img { width: 100%; border-radius: 15px; border: 1px solid #f0f0f0; box-shadow: 0 10px 30px rgba(0,0,0,0.05); }

/* Í≤ÄÏÉâ ÏúÑÏ†Ø ÏòÅÏó≠ */
.widget-area { margin-top: 30px; border-top: 2px solid #eee; padding-top: 20px; }
.widget-title { font-size: 16px; font-weight: bold; margin-bottom: 10px; color: #333; }
.search-btn { width: 100%; padding: 12px; background: #333; color: white; border: none; border-radius: 8px; font-weight: bold; cursor: pointer; margin-top: 15px; transition: background 0.2s; }
.search-btn:hover { background: #555; }
.search-btn:disabled { background: #ccc; cursor: not-allowed; }

/* Ïú†ÏÇ¨ Í≤ÄÏÉâ Í≤∞Í≥º Í∑∏Î¶¨Îìú */
.result-area { margin-top: 30px; border-top: 1px solid #eee; padding-top: 20px; }
.result-grid { display: grid; grid-template-columns: repeat(2, 1fr); gap: 10px; margin-top: 10px; }
.result-item { border: 1px solid #eee; padding: 10px; border-radius: 8px; transition: hover 0.2s; }
.result-item:hover { border-color: #42b983; background: #f9fdfb; }
.result-img { width: 100%; height: 100px; object-fit: contain; margin-bottom: 5px; background: #fafafa; }
.result-name { font-size: 11px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; color: #666; }
.result-sim { color: #42b983; font-weight: bold; font-size: 13px; }

.right-panel { flex: 1; }
.product-name { font-size: 24px; font-weight: 800; margin-bottom: 15px; color: #2c3e50; }
.product-code { color: #aaa; margin-bottom: 30px; font-size: 14px; }

h3 { font-size: 18px; margin-bottom: 20px; color: #333; border-left: 4px solid #42b983; padding-left: 12px; }

.spec-section { margin-bottom: 40px; }
.spec-grid { display: grid; grid-template-columns: 1fr 1fr; gap: 12px; background: #fff; border: 1px solid #eee; padding: 20px; border-radius: 12px; }
.spec-item { display: flex; border-bottom: 1px solid #f5f5f5; padding: 10px 0; font-size: 13px; }
.spec-key { font-weight: bold; width: 110px; color: #7f8c8d; }
.spec-val { color: #2c3e50; flex: 1; }

.chart-section { background: #fafafa; padding: 30px; border-radius: 16px; border: 1px solid #f0f0f0; }
.canvas-holder { height: 300px; width: 100%; }
</style>